.page__body {
  font-family: "Montserrat", "Arial", sans-serif;
  color: #333333;
  font-weight: 400;
  background-color: #FFFFFF;
  min-height: 100%;
}
.page__body-container {
  position: relative;
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page__body-container {
    width: 728px;
  }
}
@media (min-width: 1440px) {
  .page__body-container {
    width: 1360px;
  }
}
.header {
  position: relative;
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
}
.header__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-right: 39px;
  min-height: 60px;
}
.header__logo-container {
  width: 120px;
}
.header__logo-span {
  display: none;
}
@media (min-width: 768px) {
  .header__container {
    padding-right: 0;
    min-height: 80px;
  }
  .header__logo-image {
    width: 82px;
  }
}
@media (min-width: 1440px) {
  .header__logo-container {
    display: flex;
    flex-wrap: wrap;
    width: 350px;
  }
  .heder__logo-link {
    margin-right: 20px;
  }
  .header__logo-span {
    display: block;
    align-self: center;
  }
  .header__logo-image {
    width: 170px;
  }
}
.navigation_main--nojs .navigation__toggle {
  display: none;
}
.navigation_main--nojs {
  display: block;
  background-color: #FFFFFF;
  z-index: 5;
}
.navigation_main--opened .navigation__toggle::after {
  position: absolute;
  content: "";
  background-image: url("../../img/menu-close.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 12px;
  top: 22px;
  right: 20px;
  cursor: pointer;
}
.navigation_main--closed .navigation__toggle::after {
  position: absolute;
  content: "";
  background-image: url("../../img/menu-burger.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 12px;
  top: 22px;
  right: 20px;
  cursor: pointer;
}
.navigation__list {
  letter-spacing: 0.02em;
  text-transform: uppercase;
}
.navigation_main--closed .navigation__list {
  display: none;
}
.navigation_main--nojs .navigation__list {
  display: block;
}
.navigation__list.navigation__list--opened {
  display: block;
  position: absolute;
  top: 60px;
  width: 100%;
  right: 0;
  background-color: #FFFFFF;
  z-index: 5;
}
.navigation__list.navigation__list--nojs {
  display: block;
}
.navigation__list-item {
  position: relative;
  border-bottom: 1px solid #F0EBFF;
}
.navigation__list-item_first {
  border-top: 1px solid #F0EBFF;
}
.navigation__link {
  display: block;
  padding-top: 19px;
  padding-bottom: 20px;
  text-align: center;
  color: #333333;
}
.navigation__link-current::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #7859CF;
  bottom: 0;
  left: 0;
}
.navigation__link:hover,
.navigation__link:focus {
  color: #7859CF;
}
.navigation__link:active {
  color: #333333;
  background-color: #7859cf40;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
  .navigation__list {
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    width: 400px;
    top: 0;
    left: 48%;
    transform: translateX(-50%);
  }
  .navigation__list-item {
    border: none;
  }
  .navigation__link {
    padding-top: 32px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (min-width: 1440px) {
  .navigation__list {
    width: 460px;
    left: 52%;
  }
  .navigation__list-item:not(:last-child) {
    margin-right: 20px;
  }
  .navigation__link {
    padding-bottom: 32px;
  }
  .navigation__link:active {
    color: #593CAC;
    background-color: #7859cf40;
  }
}
.navigation-user {
  display: flex;
}
.navigation-user__button {
  position: relative;
  display: flex;
  flex-wrap: wrap;
}
.navigation-user__button-span {
  display: none;
}
@media (min-width: 1440px) {
  .navigation-user__button-span {
    display: block;
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    color: #333333;
    text-transform: uppercase;
  }
  .navigation-user__button_log-in {
    margin-right: 20px;
    width: 112px;
  }
  .navigation-user__button_car {
    width: 129px;
  }
  .navigation-user__icon {
    margin-right: 8px;
  }
}
.button {
  display: block;
  padding: 0;
  width: 113px;
  background-color: #FFFFFF;
  border-radius: 4px;
  font-size: 16px;
  color: #7859CF;
