  background-color: #FFFFFF;
}
.button_navigation-user .navigation-user__icon {
  fill: #333333;
}
.button_toggle-navigation {
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 0;
  top: 0;
  right: 0;
}
.button_toggle-navigation:active {
  background-color: #7859cf40;
}
.button_order {
  width: 113px;
  padding: 8px 20px 8px 20px;
