}
.button_catalog-filters {
  width: 160px;
  font-size: 13px;
  line-height: 24px;
}
.button_catalog-filters:hover,
.button_catalog-filters:focus,
.button_catalog-filters:active {
  color: #FFFFFF;
}
.button_navigation-user:hover .navigation-user__button-span,
.button_navigation-user:focus .navigation-user__button-span {
  color: #7859CF;
}
.button_navigation-user:active {
