  background-position: 20px 10px;
}
@media (min-width: 768px) {
  .navigation-user__button {
    min-height: 80px;
  }
  .button_toggle-navigation {
    display: none;
  }
  .button_submit {
    width: 147px;
  }
  .button_reset {
    width: 138px;
  }
  .button_catalog-filters {
    font-size: 14px;
  }
}
@media (min-width: 1440px) {
  .button_navigation-user {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 112px;
  }
  .button_navigation-user:hover .navigation-user__icon,
  .button_navigation-user:focus .navigation-user__icon {
    fill: #7859CF;
  }
  .button_toggle-navigation {
    display: none;
  }
  .button_car {
    width: 129px;
  }
  .button_submit {
    width: 151px;
  }
}
.list {
  list-style: none;
  margin: 0;
  padding: 0;
  color: inherit;
}
.link {
  cursor: pointer;
  text-decoration: none;
}
.link_catalog-products {
  color: #333333;
}
.link_pagination {
  display: block;
  width: 40px;
  height: 40px;
  padding: 10px;
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  color: #7859CF;
  background-color: #FFFFFF;
