}
.link_pagination:hover,
.link_pagination:focus {
  background-color: #9070EC;
  color: #FFFFFF;
}
.link_pagination:active {
  background-color: #593CAC;
  color: #FFFFFF;
}
.link_pagination:hover .catalog-products__pagination-icon,
.link_pagination:focus .catalog-products__pagination-icon,
.link_pagination:active .catalog-products__pagination-icon,
.link_pagination.disabled .catalog-products__pagination-icon {
  fill: #FFFFFF;
}
.link_pagination.disabled {
  background-color: #E2E2E2;
  color: #FFFFFF;
}
.link_pagination-current {
  font-weight: 600;
